@import '~materialize-css/dist/css/materialize.min.css';
@import url('https://fonts.googleapis.com/css?family=Quicksand&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: 'Quicksand', sans-serif;
  color: white;
}
h1 {
  margin: 15px 45px;
  font-weight: 600;
  font-size: 38px;
  text-transform: capitalize;
  letter-spacing: 2px;
}
h2 {
  margin: -17px 0 15px 24%;
  font-size: 19px;
}

.todo {
  margin-bottom: 1rem;
}

.container {
  background-color: #67992e;
  width: 50%;
  padding: 10px 0 20px 0;
  margin-top: 3%;
  border-radius: 5px;
  box-shadow: 2px 3px 27px -4px rgba(0, 0, 0, 0.48);
}

.todo.completed span {
  text-decoration: line-through;
  color: rgba(255, 255, 255, 0.7);
}

.todo label {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.15);
  padding: 1rem 2rem;
  color: white;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.13);
}

.todo i {
  z-index: 2;
  color: #bef574 !important;
}

.input-field {
  width: 93%;
  margin: 0 auto;
}

.input-field input {
  color: white;
  border-bottom: 2px solid #fff !important;
}
.input-field label {
  color: #bef574;
  font-weight: 600;
  letter-spacing: 1.7px;
}
